	module.exports = {"pathContext":{"docHtml":"<h1 align=\"center\">\n  <a href=\"http://chaijs.com\" title=\"Chai Documentation\">\n    <img alt=\"ChaiJS\" src=\"http://chaijs.com/img/chai-logo.png\">\n    <br>\n    get-func-name\n  </a>\n</h1>\n<p align=center>\n  Utility for getting a function's name for <a href=\"http://nodejs.org\">node</a> and the browser.\n</p>\n<p align=\"center\">\n  <a href=\"./LICENSE\">\n    <img alt=\"license:mit\" src=\"https://img.shields.io/badge/license-mit-green.svg?style=flat-square\">\n  </a>\n  <a href=\"https://github.com/chaijs/get-func-name/releases\">\n    <img alt=\"tag:?\" src=\"https://img.shields.io/github/tag/chaijs/get-func-name.svg?style=flat-square\">\n  </a>\n  <a href=\"https://travis-ci.org/chaijs/get-func-name\">\n    <img alt=\"build:?\" src=\"https://img.shields.io/travis/chaijs/get-func-name/master.svg?style=flat-square\">\n  </a>\n  <a href=\"https://coveralls.io/r/chaijs/get-func-name\">\n    <img alt=\"coverage:?\" src=\"https://img.shields.io/coveralls/chaijs/get-func-name/master.svg?style=flat-square\">\n  </a>\n  <a href=\"https://www.npmjs.com/packages/get-func-name\">\n    <img alt=\"npm:?\" src=\"https://img.shields.io/npm/v/get-func-name.svg?style=flat-square\">\n  </a>\n  <a href=\"https://www.npmjs.com/packages/get-func-name\">\n    <img alt=\"dependencies:?\" src=\"https://img.shields.io/npm/dm/get-func-name.svg?style=flat-square\">\n  </a>\n  <a href=\"\">\n    <img alt=\"devDependencies:?\" src=\"https://img.shields.io/david/chaijs/get-func-name.svg?style=flat-square\">\n  </a>\n  <br>\n  <a href=\"https://saucelabs.com/u/chaijs-get-func-name\">\n    <img alt=\"Selenium Test Status\" src=\"https://saucelabs.com/browser-matrix/chaijs-get-func-name.svg\">\n  </a>\n  <br>\n  <a href=\"https://chai-slack.herokuapp.com/\">\n    <img alt=\"Join the Slack chat\" src=\"https://img.shields.io/badge/slack-join%20chat-E2206F.svg?style=flat-square\">\n  </a>\n  <a href=\"https://gitter.im/chaijs/chai\">\n    <img alt=\"Join the Gitter chat\" src=\"https://img.shields.io/badge/gitter-join%20chat-D0104D.svg?style=flat-square\">\n  </a>\n</p>\n<h2>What is get-func-name?</h2>\n<p>This is a module to retrieve a function's name securely and consistently both in NodeJS and the browser.</p>\n<h2>Installation</h2>\n<h3>Node.js</h3>\n<p><code>get-func-name</code> is available on <a href=\"http://npmjs.org\">npm</a>. To install it, type:</p>\n<pre><code>$ npm install get-func-name\n</code></pre>\n<h3>Browsers</h3>\n<p>You can also use it within the browser; install via npm and use the <code>get-func-name.js</code> file found within the download. For example:</p>\n<pre><code class=\"language-html\">&#x3C;script src=\"./node_modules/get-func-name/get-func-name.js\">&#x3C;/script>\n</code></pre>\n<h2>Usage</h2>\n<p>The module <code>get-func-name</code> exports the following method:</p>\n<ul>\n<li><code>getFuncName(fn)</code> - Returns the name of a function.</li>\n</ul>\n<pre><code class=\"language-js\">var getFuncName = require('get-func-name');\n</code></pre>\n<h4>.getFuncName(fun)</h4>\n<pre><code class=\"language-js\">var getFuncName = require('get-func-name');\n\nvar unknownFunction = function myCoolFunction(word) {\n   return word + 'is cool'; \n};\n\nvar anonymousFunction = (function () {\n    return function () {};\n}());\n\ngetFuncName(unknownFunction) // 'myCoolFunction'\ngetFuncName(anonymousFunction) // ''\n</code></pre>","docPath":"argo-ci/node_modules/get-func-name/readme","proj":"argo-ci"}}

